import 'font-awesome/css/font-awesome.min.css';
import './assets/css/app.css';
import DashboardPage from './pages/DashboardPage';
import LoginPage from './pages/auth/LoginPage'
import ResetPassword from './pages/auth/ResetPassword';
import ProfilePage from './pages/profile/ProfilePage';
import ChangePasswordPage from './pages/profile/ChangePasswordPage';
import UserPreferencesPage from './pages/profile/UserPreferencesPage'
import {BrowserRouter as Router, Navigate, Route, Routes} from 'react-router-dom';
import Users from './pages/Users/Users';
import Reviews from './pages/Reviews/Review';
import Company from './pages/Company/Company';
import CompanyForm from './pages/Company/CompanyForm';
import CompanyFormUpdate from './pages/Company/CompanyFormUpdate';
import { useState } from 'react';

function App() {
    const [admin,setAdmin]= useState(false)
    const loginAdmin = (e)=>{
        e.preventDefault()
        let values =e.target 
        if(values.email.value==='demo@demo'&& values.password.value==="demo"){
            setAdmin(true)

        }
       
    }

  return (
        <Router>
            <Routes>
                <Route exact path='/' element={admin?<DashboardPage/>:<Navigate to={'/admin/login'}/>} />
                <Route exact path='/admin/login' element={admin?<Navigate to={'/admin'}/>:<LoginPage loginAdmin={loginAdmin}/>} />
                <Route exact path='/admin/companies' element={<Company/>} />
                <Route exact path='/admin/companies/add' element={<CompanyForm/>} />
                <Route exact path='/admin/companies/:edit' element={<CompanyFormUpdate/>} />
                <Route exact path='/admin/users' element={<Users/>} />
                <Route exact path='/admin/reviews' element={<Reviews/>} />
            </Routes>  
        </Router>
    )
}

export default App;
